import S from 'fluent-json-schema'
import {
  sBigInt,
  sBlock,
  sClientEntity,
  sPageSearchQuery,
  sReward,
  sStringId,
} from '../core'
import { Block, ClientEntity, PageSearchQuery, Reward, iBigInt } from '../core'
import { VAULT_PERFORMANCES_ROUTES_KEY } from './vault-performance.const'

/**
 * Client VaultPerformance interface
 */
export interface VaultPerformance extends VaultPerformanceData, ClientEntity {}

export function sVaultPerformance(isPartial?: boolean) {
  return S.object()
    .id('#vaultPerformance')
    .additionalProperties(false)
    .extend(sClientEntity(isPartial))
    .extend(sVaultPerformanceData(isPartial))
}

export interface VaultPerformanceData extends VaultBlockPerformance {
  vaultId: string
  vaultBlockId: string
  block: Block
}

export function sVaultPerformanceData(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('vaultId', sStringId())
    .prop('vaultBlockId', sStringId())
    .prop('block', sBlock())
    .required(isPartial ? [] : ['vaultId', 'vaultBlockId', 'block'])
    .extend(sVaultBlockPerformance(isPartial))
}

export interface VaultBlockPerformance {
  // The age of the block performance in seconds
  age: number
  // The number of the HODLers
  holders: number
  // APY realized
  realizedAPY: number
  // The earnings datas
  earnings: VaultBlockEarnings
  // The rewards data
  accruedRewards?: Reward[]
}

export function sVaultBlockPerformance(isPartial?: boolean) {
  return S.object()
    .additionalProperties(false)
    .prop('age', S.number())
    .description('The age of the block performance in seconds.')
    .prop('holders', S.number())
    .description("The vault's holders number.")
    .prop('realizedAPY', S.number())
    .description("The vault's realized APY.")
    .prop('accruedRewards', S.array().items(sReward()))
    .description('The accrued rewards of the vault.')
    .prop('earnings', sVaultBlockPerformanceEarnings())
    .description("The vault's earnings.")
    .required(
      isPartial
        ? []
        : ['age', 'holders', 'realizedAPY', 'accruedRewards', 'earnings']
    )
}

export interface VaultBlockEarnings {
  // USD amount
  USD: iBigInt
  // Token amount
  token: iBigInt
  // The percentage of the earning from the starting price
  percentage: number
}

export function sVaultBlockPerformanceEarnings() {
  return S.object()
    .additionalProperties(false)
    .prop('USD', sBigInt())
    .required()
    .prop('token', sBigInt())
    .required()
    .prop('percentage', S.number())
    .required()
}

export enum VaultPerformanceErrorCodes {
  alreadyExists = 'VAULT_PERFORMANCE_ALREADY_EXISTS',
}

export type VaultPerformanceFields =
  | '_id'
  | 'vaultId'
  | 'vaultBlockId'
  | 'block'
  | 'age'
  | 'holders'
  | 'realizedAPY'
  | 'accruedRewards'
  | 'earnings'
  | 'createdAt'
  | 'createdBy'
  | 'updatedAt'
  | 'updatedBy'

export const VAULT_PERFORMANCE_FIELDS = [
  '_id',
  'vaultId',
  'vaultBlockId',
  'block',
  'age',
  'holders',
  'realizedAPY',
  'accruedRewards',
  'earnings',
  'createdAt',
  'createdBy',
  'updatedAt',
  'updatedBy',
]

export const VAULT_PERFORMANCE_SORT_FIELDS = ['_id']

export interface VaultPerformancesSearchQuery
  extends PageSearchQuery<'_id', VaultPerformanceFields> {
  vaultId?: string | string[]
  vaultBlockId?: string | string[]
}

export function sVaultPerformancesSearchQuery() {
  return S.object()
    .additionalProperties(false)
    .prop('vaultId', S.array().minItems(1).maxItems(200).items(sStringId()))
    .description('Vault IDs of the performance that must match.')
    .prop(
      'vaultBlockId',
      S.array().minItems(1).maxItems(200).items(sStringId())
    )
    .description('Vault block IDs of the performance that must match.')
    .extend(
      sPageSearchQuery(VAULT_PERFORMANCE_FIELDS, VAULT_PERFORMANCE_SORT_FIELDS)
    )
}

export enum VaultPerformanceRoutes {
  v1Create = `v1/${VAULT_PERFORMANCES_ROUTES_KEY}`,
  v1Delete = `v1/${VAULT_PERFORMANCES_ROUTES_KEY}/:vaultPerformanceId`,
  v1Read = `v1/${VAULT_PERFORMANCES_ROUTES_KEY}/:vaultPerformanceId`,
  v1Update = `v1/${VAULT_PERFORMANCES_ROUTES_KEY}/:vaultPerformanceId`,
  v1Search = `v1/${VAULT_PERFORMANCES_ROUTES_KEY}`,
}
